:root {
  --accent-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body, header, nav, li, a, img, ul {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  margin-top: 5px;
  display: block;
}

.nav-list {
  align-items: center;
  display: flex;
}

.nav-item {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  margin: 0 10px;
  list-style-type: none;
}

.nav-item:last-child {
  margin-right: 0;
}

.nav-item:first-child {
  margin-left: 0;
}

.nav-link {
  cursor: pointer;
  color: #000;
  box-sizing: border-box;
  height: 60px;
  padding-top: 23px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.is-active, [data-qa="hover"]:hover, .nav-link:hover {
  color: var(--accent-color);
}

.is-active:after {
  content: attr(data-qa);
  border-bottom: 4px solid var(--accent-color);
  border-radius: 8px;
  width: 100%;
  display: block;
  position: relative;
  top: 19px;
}

